* {
    box-sizing: border-box;
}

.flex-container {
    display: flex;
    flex-direction: row;
    margin: 0px 90px;
}

.flex-item-left {
    flex: 30%;
    padding: 20px;
    background-color: whitesmoke;
}

.active{
    display: none;
}

.h-100{
    height: 100vh;
}

.flex-item-right {
    position: relative;
    background-color: dodgerblue;
    padding: 10px;
    flex: 70%;
    background-image: url("../images/background.jpeg");
    background-position: center;
    background-size: cover;
}

.center {
    display: flex;
    align-items: center;
    justify-content: center;
}

.statue-image {
    height: 400px;
}

