.content {
    margin: 30px 0px;
}

/*Icon Style*/
.custom-icon {
    text-align: center;
    border-radius: 50%;
    width: 35px;
    height: 35px;
    background: radial-gradient(white,darkgray);
    border: none;
    cursor: pointer;
}

.custom-icon-rect {
    text-align: center;
    border-radius: 2px;
    width: 25px;
    height: 20px;
    background: radial-gradient(white,darkgray);
    border: none;
    cursor: pointer;
    color: darkgoldenrod;
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.mb-35 {
    margin-bottom: 35px !important;
}

.text-center {
    text-align: center;
}

.text-muted {
    font-size: 12px;
    color: #333c47;
}

.title h2 {
    font-family: serif;
    letter-spacing: 2px;
    font-weight: unset;
    margin: 0px;
    padding: 0px;
}

/* .custom-icon .icon-vertical-middle {
    vertical-align: middle;
} */

.icon-group {
    display: flex;
    justify-content: space-between;
    width: 35%;
}

.details p {
    font-size: 13px;
    /* color: #333C47; */
    margin: 0px;
    padding: 0px;
}

.fs-16 {
    font-size: 16p;
}

.description {
    white-space: pre-wrap;
    overflow-wrap: break-word;
    font-size: 13px;
    color: gray;
}

.read-more {
    color: darkgoldenrod;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.read-more .icon {
    margin-right: 5px;
}

.read-more .text {
    font-size: small;
    text-decoration: none;
    color: darkgoldenrod;
}

.custom-btn .btn1 {
    text-align: center;
    text-decoration: none;
    display: inline-block;
    border: none;
    color: white;
    background: darkgoldenrod;
    padding: 10px 25px;
    border-radius: 4px;
    font-weight: 600;
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    cursor: pointer;
}

.custom-btn {
    display: flex;
    justify-content: space-evenly;
}

.custom-btn .btn2 {
    text-align: center;
    text-decoration: none;
    display: inline-block;
    border: 1px solid darkgoldenrod;
    color: darkgoldenrod;
    padding: 10px 25px;
    border-radius: 4px;
    font-weight: 600;
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    cursor: pointer;
}

.relative-pos{
    position: relative;
}
.close-open-menu{
    position: absolute;
    top: 11%;
    left: 29.5%;
}

.left-0{
    left:0 !important
}

/* Responsive layout - makes the two columns stack on top of each other instead of next to each other */
@media screen and (max-width: 600px) {
    .flex-container {
        flex-direction: column;
        margin: auto;
        height: 100vh;
    }

    /* .row {
        margin: 0px;
    }

    .column-left {
        width: 100%;
    }

    .column-right {
        width: 100%;
    } */

    .icon-group {
        width: 100%;
    }

    .statue-image {
        height: auto;
    }

    .custom-btn {
        flex-direction: column;
    }

    .custom-btn .btn1 {
        margin-bottom: 10px;
    }

    .close-open-menu{
        position: absolute;
        top: 0;
        left: 0;
    }
}